<template>
    <div id="app">
    
        <router-view></router-view>
        
    
    </div>
</template>

<script>
export default {
    name: 'app',
    data() {
        return {
        }
    },
    methods: {
        //获取底部菜单数据和主页顶部图片
        getMenuData() {
            this.$http.get('../static/json/menubar.json').then(function (res) {
                this.$store.commit('ADD_MENUBARDATA',res.body.menu_list)//调用vuex mutations同步存储底部菜单数据
                this.$store.commit('ADD_TOPBanner',res.body.topBanner)//调用vuex mutations同步存储主页顶部图片

            })
        },
        //检查cookie是否有html1705:jy_xxxxx字段，如果有，则自动登录，并且请求相应用户的购物车数据
        checkCookie(){
            //根据str向后台查找相应用户数据，若有则存到vuex里面，
                console.log(document.cookie);
                var str=document.cookie.split("=")[1];
                if(str){

                    this.$http.get("http://139.199.107.198:8888/getCart",{
                        params:{
                            username:str
                        }
                    }).then(res=>{
                        // console.table(res.body)
                        //将购物车数据存入vuex
                        this.$store.commit("ISLOGINDATA",res.body);
                        //存入用户名
                        this.$store.commit("CHANGE_USERNAME",str);
                        //改变登录标志
                        this.$store.commit("CHANGEISLOGIN",true);
                    })
                }else{
                    console.log("cookie没有账户信息")
                }
        //     var arr=document.cookie.split(";")//先用;把cookie分割成数组
        //         var obj={};//定义一个空对象用来存储cookie的键值对
                
        //         arr.forEach(function(item,index){
        //             //先要去除每个字符串里面的首尾空格,在以=号分割把每项转为数组
        //             //cookie第一条往后都会在头部添加一个空格,所以要先用trim去除字符串空格
        //             var a=item.trim().split("="); 
        //             obj[a[0]]=a[1];//分割出来的数组，第一个元素为key,第二个为value，存入对象
        //         })
        //         console.log(obj);
            },

        },
    created() {
        //检查cookie
        this.checkCookie()
        this.getMenuData()
    }

}
</script>


<style lang="scss" scoped>

</style>



