<template>
    <div id="StoreDisplay">
        <HomeHeader :topbanner="topbanner"></HomeHeader>
        <header id="head" class="head">
            <div class="fixtop">
                <span class="t-find iconfont" @click="back()"> &#xe60e; </span>
                <span class="t-index" >{{name}}</span>
                <span class="t-user iconfont " @click="flag=!flag">&#xe64b;</span>
                <div class="menu-item"  v-show="flag">
                    <div class="menu-angle">
                    </div>
                    <ul>
                        <li @click="push">
                            <a href="" >
                                <span class="iconfont icon-zhuye">
                                    &#xe6a6;
                                </span>
                                首页
                            </a>
                        </li>
                        <li>
                            <a href="">
                                <span class="iconfont icon-shoucang">
                                    &#xe61a;
                                </span>
                                我的收藏
                            </a>
                        </li>
                        <li>
                            <a href="">
                                <span class="iconfont icon-dingdan">
                                    &#xe672;
                                </span>
                                我的订单
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </header>
        <div class="brand-banner">
            <div class="brand-banner-box">
                <div class="brand_logo">
                    <img src="https://goods7.juancdn.com/bao/170601/c/8/592fc197ad0a49289b8b4597_180x90.png?iopcmd=convert&Q=88&dst=png" alt="">
                </div>
                <div class="brand_det">
                    <div class="brand_name" >
                        {{name}}
                    </div>
                    <span class="brand_del">
                        共183件商品
                    </span>
                </div>
                <div class="brand-collect">
                    <i class="iconfont iconxin">&#xe62c;</i>
                    <span>收藏店铺</span>
                </div>
            </div>
        </div>
        <div class="brand_times">
            <div> 距离结束还有</div>
            <div class="time iconfont" v-date="{ time: '1500948000',bol:true}" >
            </div>
        </div>
        <ShowCase :msg="Integrated" :shopname="name"></ShowCase>
        <OpenApp></OpenApp>
        <OpenCart></OpenCart>
        <ToTop></ToTop>
    </div>
</template>
<script>
import HomeHeader from "../../common/HomeHeader"
import ShowCase from "../ShowCase/ShowCase"
import OpenApp from "../OpenApp/OpenApp"
import OpenCart from "../OpenCart/OpenCart"
import ToTop from "../ToTop/ToTop"

export default {

    name: "StoreDisplay",
    data(){
        return {
            allData:[],
            Integrated:[],
            flag:false,
            name:this.$route.params.name,
            id:this.$route.params.id,
            page:1
        }
    },
    methods:{
        getHttp(){
            this.$http.get('http://139.199.107.198:8888/getShopData',{
                params:{
                    id:this.id,
                    page:this.page
                }
            }).then((response)=>{

                this.$nextTick(()=>{
                    console.log('storeDisplay')
                    this.Integrated = this.Integrated.concat(response.body[0].special)
                    this.allData = this.allData.concat(response.body[0].special)
                    console.log(this.allData);
                    this.$store.commit('ADD_PRICE',this.allData)
                    this.pageScroll();
                })
            },err=>{
                console.log(err)
            })
        },
        push(){
            this.$router.push({name:'Home'});
        },
        back(){
            history.back();
        },
        pageScroll(){
                var _this = this ;
                window.onscroll = function  () {
                    // 页面总高度
                    var bodyHeight = document.documentElement.offsetHeight;

                    // 可视区高度
                    var windowHeight = document.documentElement.clientHeight;
                    //滚动条的高度
                    var srcollTop = document.documentElement.scrollTop || document.body.scrollTop||window.srcollTop;
                    var srcollH = document.body.scrollHeight;
                    // alert(srcollH);
                    if (srcollTop+windowHeight >= srcollH) {

                            if(_this.page<3){
                                _this.page++ ;
                                console.log("ST"+srcollTop)
                                console.log("SH"+srcollH)
                                console.log("可视"+windowHeight)
                                 _this.getHttp() ;
                                if(_this.page==3)_this.page = 0 ;

                            }
                    }
                }
            }
    },
    created () {
        console.log(this.id);
        window.onscroll = null;
        this.getHttp();
    },

    components:{
        ShowCase,
        OpenApp,
        OpenCart,
        ToTop,
        HomeHeader
    },
    computed:{
        topbanner:function(){
            return this.$store.getters.topBanner;
        }
    }
}
</script>
<style lang="scss" scoped>
#head {
    height: 2.2rem;
    width: 100%;

    min-width: 8rem;
    overflow: inherit;
    position: relative;
    z-index: 199;
    border-bottom: 1px solid #dedede;
    .fixtop{
        background-color: #fff;
        display:flex;
        width: 100%;
        font-size: .7rem;
        text-align:center;
        height: 2.1rem;
        line-height: 2.1rem;
        .t-find{
            flex:1 1 25% ;
            font-size: 1.2rem;
            text-align: left;
            padding-left: 5px;
        }
        .t-index{
            flex:1 1 50% ;
            font-weight: 500;
            text-align: center;
            cursor: default;


            box-sizing: border-box;
            color: #333;
            font-size: .8rem;

            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }
        .t-user{
            flex:1 1 20% ;
            font-size: 1rem;
        }
        .menu-item{
            position: absolute;
            top: 2rem;
            right: -.6rem;
            width: 6rem;
            height: 6.65rem;
            font-size: .7rem;
            line-height: 2.25rem;
            border-radius: .15rem;
            background-color: rgba(0,0,0,.7);
            .menu-angle{
                width: 0;
                height: 0;
                border-left: .35rem solid transparent;
                border-right: .35rem solid transparent;
                border-bottom: .35rem solid rgba(0,0,0,.7);
                position: absolute;
                top: -.325rem;
                right: .8rem;
            }
            ul{
                -webkit-tap-highlight-color: transparent;
                li{
                    margin-left: .5rem;
                    margin-right: .5rem;
                    border-bottom: 1px solid #666;
                    a{
                        width: 100%;
                        display: block;
                        color: #fff;
                        text-align: left;
                        .icon-zhuye {
                            font-size: 1rem;

                            vertical-align: middle;
                            display: inline-block;
                            color: #fff;
                        }
                        .icon-shoucang {
                            font-size: 1rem;
                            margin-right: .25rem;
                            vertical-align: middle;
                            display: inline-block;
                            color: #fff;
                        }
                        .icon-dingdan {
                            font-size: 1rem;
                            margin-right: .25rem;
                            vertical-align: middle;
                            display: inline-block;
                            color: #fff;
                        }
                    }
                    &:last-child {
                        border-bottom: none;
                    }
                }
            }
        }
    }
}

.brand-banner{
    background: url("../../assets/bg_repeat.png") repeat;
    background-size: 1.8rem auto;
    padding-top: .6rem;
    padding-bottom: .6rem;
    .brand-banner-box{
        margin: 0 auto;
        height: 3rem;
        line-height: 3rem;
        background-color: #fff;
        display: flex;
        width: 95%;
        .brand_logo{
            width: 25%;
            padding: 5%;
            img{
                width: 100%;
                vertical-align: top;
            }
        }
        .brand_det{
            text-align: center;
            display: flex;
            width: 50%;
            flex-direction: column;
            justify-content:space-around;
            font-size: .6rem;
            line-height: 1rem;
            .brand_name{
                white-space:nowrap;
                overflow:hidden;
                text-overflow:ellipsis;
            }

        }
        .brand-collect{
            display: flex;
            flex-direction:column;
            text-align: center;
            width: 25%;
            .iconxin{
                height: 50%;
                text-align: center;
                line-height: 1.5rem;
                font-size:1rem;
            }
            span{
                height: 50%;
                text-align: center;
                line-height: 1.5rem;
            }
        }
    }
}
.brand_times {
    color: #999;
    text-align: center;
    font-size: .7rem;
    position: relative;
    padding-top: 12px;
    padding-bottom: 10px;

    text-align: center;
    line-height:1rem;
    .time {
        background: #f4f4f8;
        font-weight: 500;
        overflow: hidden;
        padding-left: 10px;
        padding-right: 10px;
        color: #333;
    }
}
</style>



