<template>
    <div class="regis">
        <div class="top-head">
            <span @click="back()"></span>
            注册
        </div>


        <div class="point">手机号由11位数字组成,密码由6位~18位大小写英文字母、_、或数字组成</div>
        <ul class="input_box">

            <li><input type="text" placeholder="请输入手机号码" autofocus @blur="blurPhone()" v-model="phone"/></li>
            <li><input type="password" placeholder="请输入密码" @blur="blurPassword()" v-model="password"/></li>

        </ul>   


        <div class="next-step" @click="sub()">下一步</div>

        <div class="msgbox" v-show="msg">{{msg}}</div>
        
    </div>
</template>

<script>
    export default {
        data(){
            return {
                msg:"",
                phone:"",
                password:""
            }
            
        },
        methods:{
            back(){
                window.history.back();
            },
            //检查手机格式
            blurPhone(){
                this.checkPhone();
            },
            //检查密码格式
            blurPassword(){
                this.checkPass();
            },
            //提交表单
            sub(){
                //全部都符合格式，上传
                if(this.checkPhone()&&this.checkPass()){
                    this.$http.get("http://139.199.107.198:8888/register",{
                        params:{
                            user:this.phone,
                            password:this.password
                        }
                    }).then((res)=>{
                        console.log(res)
                        if(res.body=="1")//服务器返回1代表该手机号未被注册
                            this.regSuccess();
                        else
                            this.regError();
                    })
                }
            },
            //检查手机格式
            checkPhone(){
                var reg=/^1[34578]\d{9}$/;
                var _this=this;
                if(!reg.test(this.phone)){
                    this.msg="手机号必须以13、14、15、17、18开头的11位数字组成";
                    setTimeout(function(){
                        _this.msg="";
                    },2000);
                    return false;
                }
                return true;  
            },
            //检查密码格式
            checkPass(){
                var _this=this;
                var reg=/^\w{6,18}$/;
                if(!reg.test(this.password)){
                    this.msg="密码由6~18位大小写英文字母、_、或数字组成";
                     setTimeout(function(){
                        _this.msg="";
                     },2000);
                     return false;
                }
                return true;
            },
            //注册成功
            regSuccess(){
                var _this=this
                this.msg="注册成功";
                setTimeout(function(){
                    _this.msg="";
                },2000);
            },
            //注册失败
            regError(){
                var _this=this
                this.msg="手机号已经被注册，请换个手机号";
                setTimeout(function(){
                    _this.msg="";
                },2000);
            }
        }
    }
</script>

<style lang="scss" scoped>
    .regis{
        background: white;
        height: 1000px;
    }
    .top-head{
        height: 1.8rem;
        font-size:18px;
        line-height: 1.8rem;
        text-align: center;
        color:#333;;
        position:relative;
        border-bottom:1px solid #f2f2f2;
        span{
            position:absolute;
            width:24px;
            height: 24px;
            background:url(../assets/btn-back.png)no-repeat;
            background-size: 24px 24px;
            left: .5rem;
            top: .4rem;
        }
    }
    

    .point{
        height: 3rem;
        text-align: center;
        line-height: 3rem;
        color: #999;
    }
    .input_box{
        li{
            width: 94%;
            border-bottom:1px solid #f2f2f2;
            margin:0 auto;
            input{
                border:0;
                width:100%;
                height: 1.8rem;
                font-size:14px;
            }
        }
    }


    .next-step{
        text-align: center;
        height: 1.8rem;
        background: #ff464e;
        line-height: 1.8rem;
        width: 94%;
        margin:0 auto;
        font-size:18px;
        color: white;
    }


    .msgbox{
        position:fixed;
        bottom:22rem;
        height: 2.5rem;
        width: 100%;
        background: rgba(0,0,0,.6);
        margin:0 auto;
        color: white;
        line-height: 2.5rem;
        text-align: center;
        font-size:15px;
    }
</style>



