<template>
    <div id="home">
        <!--加载动画的显示与消失  -->
        <Loading :loadingFlag="loadingFlag"></Loading>

        <div class="main-box" v-show="!loadingFlag">
                <!--头部图片组件-->
            <HomeHeader :topbanner="topbanner"></HomeHeader>

            <!--搜索框部分-->
            <div class="search" @click="goSearch">
                <div class="search_box">
                    <span class="icon"></span><span>搜索商品</span>
                </div>
                <span class="sort-menu"></span>
            </div>

            <!--通用滑动导航组件-->
            <SlideNav :topMenu="topMenu" :actIndex="0"></SlideNav>

            <!--轮播图组件-->
            <swiper :options="swiperOption" ref="mySwiper">-->
                <!--slides -->
                <swiper-slide v-for="item in bannerData" :key="item.pic">
                    <img :src="item.pic">
                </swiper-slide>
                <!-- Optional controls -->
                <!-- <div class="swiper-pagination" slot="pagination"></div> -->
            </swiper>

            <!--主页轮播图下方导航-->
            <div class="multi-block1 common-floor">
                <router-link :to="'/CommonTwoImg/'+item.words" v-for="item in multi_block1" :key="item.pic">
                    <img :src="item.pic">
                    <p>{{item.words}}</p>
                </router-link>
            </div>

            <!--主页3张图-->
            <div class="multi-block2 common-floor clear">
                <router-link to="/Zhifacang">
                    <img :src="multi_block2[0].pic">
                </router-link>
                <div>
                    <img :src="multi_block2[1].pic" @click="goto()">
                    <router-link to="/home">
                        <img :src="multi_block2[2].pic">
                    </router-link>
                </div>
            </div>
            <!--10点更新-->
            <div class="multi-block3">
                <router-link to="/" v-for="item in multi_block3" :key="item.pic">
                    <img :src="item.pic">
                </router-link>
            </div>

            <!--滚动刷新店铺模块-->
            <ShopShow :showData="showData"></ShopShow>

            <!--底部菜单组件-->
            <MenuBar :menubarData="menubarData"></MenuBar>


            <!--回到顶部组件-->
            <div class="back-top"><ToTop></ToTop></div>
        </div>
    </div>
</template>

<script>
    //引入通用滑动菜单组件
    import SlideNav from '../common/SlideNav.vue'
    //主页header图片
    import HomeHeader from '../common/HomeHeader.vue'
    //通用滚动店铺组件
    import ShopShow from '../common/ShopShow.vue'
    // 引入回到顶部组件
    import ToTop from '../pages/ToTop/ToTop.vue'
    //引入底部菜单模块
    import MenuBar from '../common/MenuBar'
    //轮播图组件
    import {
        swiper,
        swiperSlide
    } from 'vue-awesome-swiper'
    export default {
        data() {
            return {
                //主页头部图片
                // topbanner: '',
                //主页轮播图数据
                bannerData: [],
                //主页导航菜单数据
                topMenu: [],
                //轮播图下方导航模块
                multi_block1: [],
                //3张图模块
                multi_block2: [{
                    pic: ''
                }, {
                    pic: ''
                }, {
                    pic: ''
                }],
                //8点更新模块
                multi_block3: [],
                //滚动加载店铺数据
                showData: [],
                jsonpPage: '1',
                //轮播图配置数据
                swiperOption: {
                    autoplay: 1000,
                    direction: 'horizontal',
                    pagination: '.swiper-pagination',
                    loop: true,
                    autoHeight: true,
                    observer: true,//修改swiper自己或子元素时，自动初始化swiper
                    observeParents: true,
                    // notNextTick: true,
                },
                loadingFlag:true
            }
        },
        methods: {
            //获取展示区域数据
            getShowData() {
                this.$http.jsonp('https://shop.juanpi.com/zxg', {
                    //请求参数
                    params: {
                        key: 'zuixinzhekou',
                        type: '1',
                        zhouyi_ids: 'p8_c3_l1_18_51_5',
                        machining: 'gsortzxg',
                        page: this.jsonpPage,
                        rows: '10',
                        dtype: 'JSONP',
                        cm: '1',
                        cm_channel: '1'
                    }
                    //回调函数名字  jsonp:'callback'
                }).then(function(res) {
                    this.showData = this.showData.concat(res.body.list);
                    console.log('showdata')
                    console.log(this.showData)
                })
            },
            //获取主页头部到展示区之间的数据
            getTopData() {
                this.$http.get('../../static/json/homeTop.json').then(function(res) {

                    // this.topbanner = res.body.topbanner.pic_url;
                    this.topMenu = res.body.homeNav;
                    this.bannerData = res.body.banner_ads;
                    this.multi_block1 = res.body.module_ads.multi_block[0].data[0].child;
                    //console.log(this.multi_block1);
                    this.multi_block2 = res.body.module_ads.multi_block[1].data[0].child;
                    this.multi_block3 = res.body.module_ads.multi_block[2].data[0].child
                })
            },
            //滚动加载函数
            scrollLoad() {
                var _this = this;
                var base = 81 * parseFloat(document.documentElement.style.fontSize);
                var diff = 26 * parseFloat(document.documentElement.style.fontSize);
                console.log(document.body.children[0].offsetHeight)
                window.onscroll = function() {
                    var dis = window.scrollTop || document.body.scrollTop || document.documentElement.scrollTop;

                    if ((dis - diff) / base >= _this.jsonpPage) {
                        console.log("进入")
                        _this.jsonpPage++;
                        _this.getShowData();
                    }

                }

            },
            //美食跳转
            goto(){
                this.$router.push({
                        path:"/DressToFoods",
                        query:{
                            id:8
                    }
                })
            },
            goSearch(){
                this.$router.push({name:"search"})
            }

        },
        computed:{
            topbanner:function(){
                return this.$store.getters.topBanner
            },
            menubarData:function(){
            return this.$store.getters.menubarData
            }
        },
        components: {
            // Ref,
            SlideNav,
            HomeHeader,
            ShopShow,
            swiper,
            swiperSlide,
            MenuBar,
            ToTop
        },
        created() {
            window.onscroll = null;
            this.getTopData();
            this.getShowData();
            this.scrollLoad();
            //console.log(this.jsonpPage)
        },
        mounted(){
            //渲染完毕后延时一段时间消失loading动画
            setTimeout(()=>{
                 this.loadingFlag=false
            },800)

        }
    }
</script>

<style lang="css">
    #home {
        height: 100%;
        color:#666;
        background-color:#f4f4f8;
    }

    .search {
        height: 44px;
        background: white;
        padding-top: 1px;
        position:relative;
    }
    .search>span{
        width:28px;
        height: 28px;
        position:absolute;
        right: .45rem;
        top: .35rem;
        background: url(../assets/fenlei.png)no-repeat;
        background-size:28px 28px;
        display: inline-block;
    }
    .search_box {
        height: 28px;
        line-height: 28px;
        margin-top: 10px;
        border-radius: .17067rem;
        color: #666;
        margin-right: 1.87733rem;
        margin-left: .59733rem;
        font-size: 12px;
        background-color: #f2f2f2;
    }
    .search_box>.icon{
        width: 15px;
        height: 15px;
        display: inline-block;
        vertical-align: top;
        background: url(../assets/serach.png)no-repeat;
        background-size:15px 15px;
        vertical-align: top;
        margin-top:6px;
        margin-right:5px;
        margin-left:5px;
    }

    .swiper-container {
        width: 100%;
        font-size: 0;
        /* position: relative;
        z-index: 1000; */
        overflow: hidden;
    }

    .swiper-wrapper {
        /* font-size: 0; */
        width: 700% !important;
    }
    .swiper-slide{
        float: left !important;
    }
    .swiper-container img {
        width: 100%;
        height: auto;
    }

    .common-floor {
        margin-bottom: .5rem;
    }

    .multi-block1 {
        background: white;
        display: -webkit-flex;
        justify-content: space-around;
    }

    .multi-block1>a {
        display: inline-block;
        margin-top: .4rem;
        margin-bottom: .5rem;
    }

    .multi-block1>a>img {
        width: 40px;
        display: block;
        margin: 0 auto .42667rem;
    }

    .multi-block1>a>p {
        font-size: 12px;
        text-align: center;
        color: #666;
    }

    .multi-block2 a {
        display: block;
    }

    .multi-block2 img {
        box-sizing: border-box;
    }

    .multi-block2>a {
        width: 50%;
        float: left;
    }

    .multi-block2>a>img {
        width: 100%;
        border-right: 1px solid #f2f2f2
    }

    .multi-block2>div {
        float: left;
        width: 50%;
    }

    .multi-block2>div img {
        width: 100%
    }

    .multi-block2>div img:first-of-type {
        border-bottom: 1px solid #f2f2f2;
        border-right: 1px solid #f2f2f2;
    }

    .multi-block3>a {
        display: block;
    }

    .multi-block3>a>img {
        width: 100%;
    }


    .back-top>.slide-box{
        bottom: 3rem;
    }

</style>



