<template>
    <div id="back_top" class="slide-box" @click.stop="scrollTop" v-show="bol" ref="back" >
       <p>
           <img src="https://jp.juancdn.com/jpwebapp_v1/images_v1/icon/back-top.png?ts=390cfa98cd666aa7_1499764828-1&sv=a8351a1e">

       </p>
         
       <h1>{{money}}</h1>
   </div>
</template>
<script>
export default {
    data(){
        return {
            bol:false,
            price:50
        }
    },
    computed:{
        money(){
            return this.$store.getters.toTopBol
        }
    },
    methods:{
        scrollTop(){
            var timer = setInterval(()=>{
                var scrTop=document.documentElement.scrollTop || document.body.scrollTop;
                var ispeed=(0-scrTop)/5;
                ispeed = ispeed > 0 ? Math.ceil(ispeed) : Math.floor(ispeed);
                document.body.scrollTop = document.documentElement.scrollTop = scrTop + ispeed;
                if(scrTop<=10){
                   this.bol = false ;
                   clearInterval(timer);
                }
            },20);
        }
    },
    created(){
        var _this =this
        window.addEventListener("scroll",function(){
            var winHeight=document.body.clientHeight/2;
            var scrTop=document.documentElement.scrollTop||document.body.scrollTop;
            if (winHeight<scrTop) {
                _this.bol = true ;
            }else {
                _this.bol = false ;
            }
        })
    }
}
</script>
<style lang="scss" scoped>
.slide-box {

    position: fixed;
    bottom: .5rem;
    right: 4%;
    margin-left: 34%;
    z-index: 300;

    p {
        width: 2rem;
        height: 2rem;
        border-radius: 2rem;
        text-align: center;
        display: block;
        margin-bottom: .05rem;
        background: #fff;
        border: 1px solid #ebebeb;
        img {
            width: 1.2rem;
            height: 1.2rem;
            margin-top: .4rem;
        }
    }
}
</style>



