<template>
    <div id="search" class="clearfix">
        <!--加载动画的显示与消失  -->
        <Loading :loadingFlag="loadingFlag"></Loading>

        <header id="head" class="head">
            <div class="fixtop">
                <span class="t-find iconfont" @click="back()"> &#xe60e; </span>
                <span class="t-index" >全部分类</span>
                <span class="t-user iconfont " @click="flag=!flag">&#xe64b;</span>
                <div class="menu-item"  v-show="flag">
                    <div class="menu-angle">
                    </div>
                    <ul>
                        <li @click="push">
                            <a href="" >
                                <span class="iconfont icon-zhuye">
                                </span>
                                首页
                            </a>
                        </li>
                        <li>
                            <a href="">
                                <span class="iconfont icon-shoucang">
                                </span>
                                我的收藏
                            </a>
                        </li>
                        <li>
                            <a href="">
                                <span class="iconfont icon-dingdan">
                                </span>
                                我的订单
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </header>
        <div id="search-box">
            <a >
                <div class="box-search">
                    <input type="text" id="keyword"  placeholder="搜索商品"  value=""  v-model="t1" @keyup="get($event)">
                </div>
                <button id="search-submit" type="submit">
                    <img src="//jp.juancdn.com/jpwebapp_v1/images_v1/icon/search.png?f4e07c7d-1&amp;sv=a8351a1e">
                </button>
            </a>
        </div>
        <div class="list-left">
            <ul class="list-title">
                <li class="bc" v-for="(elem,index) in JsonpData" :key="elem.key" @click="navList(index)" :class="{'active':index==num}">
                    {{elem.name}}
                </li>
            </ul>
        </div>
        <div class="list-right">
            <div class="wraper">
                <div class="third-cate">
                    <div class="cate-second" v-for="elem in JsonpRI"  @click="goto(elem.name)">
                        <a >
                            <img :src="url+elem.icon" alt="">
                            <span>{{elem.name}}</span>
                        </a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

export default {
    data(){
        return{
            flag:false,
            JsonpData:[],
            JsonpRI:[],
            url:'http://s2.juancdn.com',
            num:0 ,
            t1:'',
            loadingFlag:true
        }
    },
    methods: {
        push() {
            this.$router.push({name:"Home"})
        },
        goto(text){
            this.$router.push({name:'Zhifacang',params:{uname:text}});

        },
        getData(){
            this.$http.jsonp("http://m.juanpi.com/cate/catelist?pf=8&area=4&bi=222&dtype=jsonp&_=1500296727742&callback=jsonp2").then(function(res){
                this.JsonpData = res.body ;
                this.JsonpRI = res.body[this.num].secondCateList
                console.log(this.JsonpData);
            },function(err){

            });
        },
        navList(index){
            this.num = index ;

            this.JsonpRI = this.JsonpData[this.num].secondCateList
        },
        get(ev){

            if(ev.keyCode==13){
                location.assign('http://m.juanpi.com/search?keyword='+this.t1);
                this.t1='';
            }
        },
        back(){
            history.back();
        }

    },
    created(){
        this.getData();
    }
    ,
    mounted(){
        //渲染完毕后延时一段时间消失loading动画
        setTimeout(()=>{
             this.loadingFlag=false
        },800)

    }
}
</script>
<style lang="scss" scoped>
#search{
    height: 100%;
    background: #fff;
}
#head {
    height: 2.2rem;
    width: 100%;

    min-width: 8rem;
    overflow: inherit;
    position: relative;
    z-index: 199;
    border-bottom: 1px solid #dedede;
    .fixtop{
        background-color: #fff;
        display:flex;
        width: 100%;
        font-size: .7rem;
        text-align:center;
        height: 2.1rem;
        line-height: 2.1rem;
        .t-find{
            flex:1 1 25% ;
            font-size: 1.2rem;
            text-align: left;
            padding-left: 5px;
        }
        .t-index{
            flex:1 1 50% ;
            font-weight: 500;
            text-align: center;
            cursor: default;


            box-sizing: border-box;
            color: #333;
            font-size: .8rem;

            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }
        .t-user{
            flex:1 1 20% ;
            font-size: 1rem;
        }
        .menu-item{
            position: absolute;
            top: 2rem;
            right: -.6rem;
            width: 6rem;
            height: 6.65rem;
            font-size: .7rem;
            line-height: 2.25rem;
            border-radius: .15rem;
            background-color: rgba(0,0,0,.7);
            .menu-angle{
                width: 0;
                height: 0;
                border-left: .35rem solid transparent;
                border-right: .35rem solid transparent;
                border-bottom: .35rem solid rgba(0,0,0,.7);
                position: absolute;
                top: -.325rem;
                right: .8rem;
            }
            ul{
                -webkit-tap-highlight-color: transparent;
                li{
                    margin-left: .5rem;
                    margin-right: .5rem;
                    border-bottom: 1px solid #666;
                    a{
                        color: #fff;
                        .iconfont {
                            font-size: 1.2rem;
                            margin-right: .25rem;
                            vertical-align: middle;
                            display: inline-block;
                            color: #fff;
                        }
                    }
                    &:last-child {
                        border-bottom: none;
                    }
                }
            }
        }
    }
}
#search-box {

    position: relative;
    height: auto;
    overflow: hidden;
    margin: .5rem;
    border: 1px solid #bbb;
    border-radius: .25rem;
    background-color: #fff;
    padding: .125rem 0;
    .box-search {
        width: 98%;
        position: relative;
        margin-left: .3rem;
        height: 1.2rem;
        line-height: 1.2rem;
        padding: 0;
        vertical-align: middle;
        background-color: #fff;
        color: #999;
        float: left;
        input {
            margin-left: 1.5rem;
            -webkit-tap-highlight-color: transparent;
            -webkit-appearance: none;
            color: #999;
            font-size: .65rem;
            width: 90%;
            left: 0;
            background-color: transparent;

            font-size: .8rem;
            color: initial;
            letter-spacing: normal;
            word-spacing: normal;
            text-transform: none;
            text-indent: 0;
            text-shadow: none;
            display: inline-block;
            text-align: start;
            border: none;
            outline-style: none;
            word-break: normal;
            vertical-align: middle;
            height: 1.2rem;
            line-height: 1.2rem;
            margin-left: 1.25rem;
            color: #666;
            position: absolute;
        }
    }
    button {
        top: .125rem;
        display: block;
        position: absolute;

        left: .3rem;
        border: 0 none;
        background: none;
        cursor: pointer;
        img {
            height: 1.2rem;
            vertical-align: middle;
        }
    }
}
.list-left {
    float: left;
    width: 25%;
    background: #f9f9f9;
    overflow: hidden;
    font-size: .7rem;
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
        text-align: left;
        text-indent: .75rem;
        li {
            height: 2.25rem;
            line-height: 2.25rem;
            position: relative;
            border-bottom: 1px solid #ebebeb;
            cursor: pointer;
            border-left: .2rem solid #f9f9f9;
        }
        .active{
            color: #ff464e;
            border-left: 4px solid #ff464e;
            background-color: #fff;
        }
    }
}
.list-right {
    float: left;
    width: 71%;
    font-size: .6rem;
    padding-left: 4%;
    .wraper {
        height: 100%;
        width: 100%;
        text-align: left;
        overflow: hidden;
        .third-cate {
            width: 100%;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: horizontal;
            -webkit-box-direction: normal;
            -ms-flex-direction: row;
            flex-direction: row;
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
            margin-top: .5rem;
            .cate-second {
                display: inline-block;
                text-align: center;
                width: 3.63rem;
                height: 4rem;
                padding: 0 5%;
                box-sizing: border-box;
                position: relative;
                a{
                    img {
                        width: 100%;
                    }
                    span {
                        color: #333;
                        line-height: 1rem;
                    }
                }
            }
        }
    }
}
</style>



