<template>
    <div class="pay-type">
        <h5>
            支付方式
            <span class="needMoney fr">
            </span>
        </h5>
        <ul class="type" id="pay-type-list">
            <li data-pay-type="12" class="active">
                <img src="//jp.juancdn.com/jpwebapp/images/shopping/icon_wx.png">
                <span>
                    微信支付
                </span>
                <span class="payway-tips-ad">
                    推荐
                </span>
                <i class="pay-type-radio" :class="{'active':bol}" @click="payBol">
                </i>
            </li>
            <li data-pay-type="5" class="">
                <img src="//jp.juancdn.com/jpwebapp/images/shopping/icon_ali.png">
                <span>
                    支付宝支付
                </span>
                <span class="payway-tips-text">
                </span>
                <i class="pay-type-radio  " :class="{'active':!bol}" @click="payBol">
                </i>
            </li>
        </ul>
        <input type="hidden" id="pay_type" name="pay_type" value="">
    </div>
</template>
<script>
export default {
    data(){
        return{
            bol:true
        }
    },
    methods: {
        payBol(){
            this.bol = !this.bol
        }
    }
}
</script>
<style lang="scss" scoped>
.pay-type {
    background: #fff;
    padding: 0 4%;
    color: #333;
    border-top: 1px solid #ebebeb;
    h5 {
        height: 2.4rem;
        line-height: 2.4rem;
        font-size: .8rem;
        font-weight: 400;
        border-bottom: 1px solid #ebebeb;

    }
    li{
        height: 2.4rem;
        line-height: 2.4rem;
        img {
            width: 1.2rem;
            height: 1.2rem;
            vertical-align: middle;
            margin-right: .4rem;
        }
        span {
            position: relative;
            font-size: .7rem;
        }
        .payway-tips-ad {
            padding: .1rem .225rem .05rem;
            border: 1px solid #ff464e;
            color: #ff464e;
            font-size: .6rem;
        }
        .pay-type-radio{
            float: right;
            width: 1rem;
            height: 1rem;
            border-radius:.9rem;
            margin-top: .75rem;
            border: 1px solid #999;
        }
        .active{
            background: url("../../assets/radio-checked.png") no-repeat;
            background-size: cover;
            border: none;
            width: 1.1rem;
            height: 1.1rem;
        }
    }
}

</style>



