import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);
/**
 * 导出vuex 实例
 * 在mian.js 接收
 */
export default new Vuex.Store({
    state: {
    //数据存储
    //排序用的比较数据
        ShopObj:[],
    //首页头部图片存储
        topBanner:'',
        //底部菜单栏数据
        menubarData:[
            {
                bgicon:''
            },
            {
                bgicon:''
            },
            {
                bgicon:''
            },
            {
                bgicon:''
            },
            {
                bgicon:''
            }
        ],
        //购物车数据暂存
        cartData:[],
        flag:false,
        //用户名信息
        username:"",
        //判断是否登录状态
        islogin:false,
        price:0 ,
        num:0,
        //全选默认true
        isAllCheck:'true',
        Stock : true

    },
    mutations: {
    //同步方法
        ADD_PRICE(state, allData){
            state.ShopObj = allData ;
        },
        ADD_TOPBanner(state,url){
            state.topBanner=url;
        },
        ADD_MENUBARDATA(state,data){
            state.menubarData=data;
        },
        ADD_CARTDATA(state,data){
            state.flag = false ;
            state.Stock = true ;
            console.log(state.cartData);
            state.cartData.forEach(function(item){
                if(item.goodsid==data.goodsid&&item.color==data.color&&item.size==data.size){

                    if(item.count+data.count<=5){
                        item.count =item.count+data.count ;

                    }else{
                        state.Stock = false ;
                        item.count = 5 ;
                        alert("最多购买5个")
                    }
                        state.flag = true ;
                }
            });
            if(state.flag==false){
                //不存在
                state.cartData.push(data);
                // console.log(state.cartData);

            }
        },
        REDUCE_CARTDATA(sata,data){
            var num = sata.cartData.indexOf(data);

    		if(sata.cartData[num].count==1){
                sata.cartData[num].count=1 ;
            }

        },
        ADDSHOPCARTDATA(sata,data){
            var num = sata.cartData.indexOf(data);

    		if(sata.cartData[num].count==5){
                sata.cartData[num].count=5 ;
            }

        },
        CHANGE_USERNAME(state,data){
            state.username=data;
        },
        // 登录后购物车数据存储
        ISLOGINDATA(state,data){

            state.cartData = data ;
        },
        //修改登录状态标志
        CHANGEISLOGIN(state,bol){
            state.islogin = bol;
        },
        //全选开关
        CHECKEDBOL(state,elem){
            var num = state.cartData.indexOf(elem);
            // console.log(num);
            if(state.cartData[num].checked=="true"){
                state.cartData[num].checked="false"
            }else if(state.cartData[num].checked=="false")
                state.cartData[num].checked="true"
        },
        ALLCHECKED(state){

            if(state.isAllCheck == "false" ){

                state.isAllCheck = "true" ;
                state.cartData.map(function(item){
                    item.checked = state.isAllCheck ;
                })
                return
            }
            if(state.isAllCheck == "true" ){

                state.isAllCheck = "false" ;
                state.cartData.map(function(item){
                    item.checked = state.isAllCheck ;
                })
                return
            }

        },
        DELETE(state,elem){

            var num  = state.cartData.indexOf(elem);


            state.cartData.splice(num,1);
        }
    },
    actions:{ //异步操作共享数据的方法
        totop(context,price){
            setTimeout(function(){
                context.commit('TOTOP',price);
            }, 1000);
        }
    },

    getters: {
        allData(state){
            return state.ShopObj;
        },
        topBanner(state){
            return state.topBanner;
        },
        menubarData(state){
            return state.menubarData;
        },
        toTopBol(state){
            return state.bol
        },

        getCartData(state){

            return state.cartData
        },
        userName(state){
            return state.username;
        },
        setIslogin(state){
            return state.islogin;
        },
        lengthBol(state){
            return state.cartData.length
        },
        allprice(state){
            state.price = 0 ;
            state.cartData.map(function(item){
                if(item.checked=='true' ){
                    state.price= state.price +item.count*item.price.substring(1);
                }
            })
            return state.price ;
        },
        allnum(state){
            state.num = 0 ;
            state.cartData.map(function(item){
                if(item.checked=='true' ){
                    state.num= state.num +parseInt(item.count)
                }
            })
            return state.num ;
        },
        isAllChecked(state){
            var bol = 1
            state.cartData.map(function(item){
                if(item.checked=="false"){
                    return bol = -1
                }
            })

            if(bol==1){
                state.isAllCheck = "true"
            }else if(bol==-1){
                state.isAllCheck = "false"
            }
                // console.log(state.isAllCheck);
                return  state.isAllCheck
        },
        getStock(state){
            return state.Stock
        }
    }
})



