<template>
	<div class="loading-box" v-show="loadingFlag">
            <div class="loading-top"></div>
            <div class="loading2">
				<img src="https://jp.juancdn.com/jpwebapp/images/icon/loading_bag.png?ts=390cfa98cd666aa7_1499935927-1&sv=a8351a1e">
				<div class="circle-rotating-new"></div>
     		</div>
    </div>
</template>


<script>
export default {
	props:['loadingFlag']
}
</script>

<style lang="css" scoped>
	.loading-box{


        height: 100%;
        background-color: rgba(255,255,255);
        height: 1000px;
    }
    .loading-box>.loading-top{
        background: url(../../assets/jp01_logo_ai.png) no-repeat center center;
        height: 2rem;
        background-color: white;
        background-size: 2rem 1.3rem;
    }
    .loading2{
		width: 100px;
	    height: 100px;
	    margin: 0 auto;
	    position: fixed;
		 filter: red(100%);
	   /* overflow: hidden;*/
	    /*background-color: black;*/
	    /*border-radius: .21333rem;*/
		left:50%;
		top: 50%;
		margin-left:-50px;
		margin-top: -50px;
		border-radius: 50%;
	    background-color: white;
		z-index: 1000;

	}
	.loading2>img{
		width:100%;
	}
	.circle-rotating-new{
		width: 50px;
	    height: 50px;
	    left: 23%;
	    top: 23%;
	    border-radius: 50%;
	    /*border:solid;*/
	    border-top-style: solid;
	    border-right-style: solid;
	    border-width: 2px;
	    box-sizing: border-box;
	    border-top-color: #ff464e;
	    border-right-color: #ff464e;
	    position: absolute;
	    animation: circle_rotating .7s linear infinite forwards;

	}


	@keyframes circle_rotating{
		0% {
		    /*-webkit-transform: rotate(0);*/
		    transform: rotate(0);
		}
		100% {
		    /*-webkit-transform: rotate(1turn);*/
		    transform: rotate(1turn);
		}
	}
</style>



