<template>
<div>

    <Loading :loadingFlag="loadingFlag"></Loading>
    <zhifacang :getData="getData" :name="name"></zhifacang>
     <!--回到顶部组件-->
    <div class="back-top"><ToTop></ToTop></div>
</div>
</template>

<script>
    import zhifacang from '../common/zhifacang'
     // 引入回到顶部组件
    import ToTop from '../pages/ToTop/ToTop.vue'
    export default {
        data(){
            return {
                getData:[],
                jsonpPage:1,
                loadingFlag:true,
                name:this.$route.params.uname,
            }
        },
        methods:{
            getdata(){
                this.$http.jsonp('https://shop.juanpi.com/gsort',{
                    params:{
                        key:'%7B"cdt"%3A%7B"stock"%3A"2"%2C"is_show_presale"%3A"0"%2C"hot_type"%3A%5B"1"%2C"3"%5D%2C"source_type"%3A%5B"1"%5D%2C"fcate"%3A"490"%2C"sale_type"%3A2%7D%2C"order"%3A%7B"show_stime"%3A"desc"%2C"sales"%3A"desc"%7D%7D',
                        type:'50',
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'danpin',
                        page:this.jsonpPage,
                        rows:'10',
                        dtype:'JSONP',
                    }
                }).then(res=>{
                    console.log(res.body)

                    this.getData=this.getData.concat(res.body.list);
                    this.pageScroll();

                    // this.showData.concat(res.body.list)
                })
            },
            pageScroll(){
                var _this = this ;
                window.onscroll = function  () {
                    // 页面总高度
                    var bodyHeight = document.documentElement.offsetHeight;

                    // 可视区高度
                    var windowHeight = document.documentElement.clientHeight;
                    //滚动条的高度
                    var srcollTop = document.documentElement.scrollTop || document.body.scrollTop||window.srcollTop;
                    var srcollH = document.body.scrollHeight;
                    // alert(srcollH);
                    if (srcollTop+windowHeight >= srcollH) {
                            _this.jsonpPage++ ;
                            console.log("ST"+srcollTop)
                            console.log("SH"+srcollH)
                            console.log("可视"+windowHeight)
                                _this.getdata() ;

                        }
                    }
            }
        },
        components:{
            zhifacang,
            ToTop
        },
        created(){
            console.log('ok')
            window.onscroll=null;
            this.getdata();
            // this.scrollLoad();
        },
        mounted(){
            setTimeout(()=>{
                this.loadingFlag=false;
            },800)
        }
    }
</script>

<style lang="scss" scoped>
    .back-top>.slide-box{
        bottom: 3rem;
    }
</style>



