<template>
<div class="mine">

    <Loading :loadingFlag="loadingFlag"></Loading>

    <div class="top-head">
        <span @click="back()"></span> 个人中心
        <b @click="logout" v-show="username">退出</b>
    </div>

<div class="top-foot" v-show="!username">
<p><span @click="goRegis">注册</span><span>|</span><span @click="goLogin">登录</span></p>
</div>
  <div class="top-login-foot" v-show="username">
    <span>{{username}}</span>

  </div>


  <div class="my-order common-margin-bottom">
    <p class="order-top"><span class="first">我的订单</span><span class="last">全部订单&gt;</span></p>

    <div class="multi-block1 common-floor">
      <router-link to="/">
        <span class="iconfont">&#xe60b;</span>
        <p>代付款</p>
      </router-link>
      <router-link to="/">
        <span class="iconfont">&#xe617;</span>
        <p>代成团</p>
      </router-link>
      <router-link to="/">
        <span class="iconfont">&#xe692;</span>
        <p>代收货</p>
      </router-link>
      <router-link to="/">
        <span class="iconfont">&#xe61b;</span>
        <p>退款/售后</p>
      </router-link>
    </div>
  </div>

  <div class="my-order common-margin-bottom" v-for="item in data1">
    <p class="order-top " v-for="value in item"><span class="first">{{value}}</span><span class="last">&gt;</span></p>
  </div>


  <div class="my-order common-margin-bottom">
    <p class="order-top"><span class="v">{{data2[0]}}</span><span class="v">{{data2[1]}}</span><span class="v">{{data2[2]}}</span></p>
  </div>

  <div class="footer"></div>

  <MenuBar :menubarData="menubarData"></MenuBar>
</div>
</template>

<script>
  import MenuBar from '../common/MenuBar'
  export default {

    data() {
      return {
        data1: [
          ["我的收藏", "我的拼团", "vip钻石卡", "优惠券"],
          ["客服中心", "关于卷皮"]
        ],
        data2: ["返回首页", "客户端", "电脑版"],
        loadingFlag: true,
        imageUrl: ''
      }
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      back() {
        window.history.back();
      },
      //去注册
      goRegis() {
        this.$router.push({
          name: 'Register'
        })
      },
      //去登录
      goLogin() {
        this.$router.push({
          name: 'Login'
        })
      },
      //退出/注销账户
      logout() {
        //销毁vuex中用户名、购物车数据、登录状态标识
        this.$store.commit("CHANGE_USERNAME", "");
        this.$store.commit("ISLOGINDATA", []);
        this.$store.commit("CHANGEISLOGIN", false);
      }
    },
    computed: {
      menubarData: function() {
        return this.$store.getters.menubarData
      },
      //判断是否登录，登陆后，username会有值
      username: function() {
        return this.$store.getters.userName;
      }
    },
    components: {
      MenuBar
    },
    mounted() {
      setTimeout(() => {
        this.loadingFlag = false;
      }, 800)
    }
  }
</script>

<style lang="scss" scoped>
    .router-link-active span{
        font-size: 1.5rem;
        font-weight: 500;
    }
  .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .el-upload:hover {
    border-color: #20a0ff;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  .common-margin-bottom {
    margin-bottom: 0.4rem;
  }

  .top-head {
    height: 1.5rem;
    background-color: #ff464e;

    font-size: 0.9rem;
    line-height: 1.9rem;
    text-align: center;
    color: white;
    position: relative;
    span {

      position: absolute;
      width: 1.4rem;
      height: 1.4rem;
      background: url(../assets/mine_back.png)no-repeat;
      background-size: 1.4rem 1.4rem;
      left: 0.5rem;
      top: 0.2rem;
    }
    b {
      font-weight: normal;
      font-size: 14px;
      position: absolute;
      line-height: 1.5rem;
      right: 0.3rem;
      top: 0;
    }
  }

  .top-foot {
    height: 4rem;
    background-color: #ff464e;
    font-size: 20px;
    color: white;
    display: -webkit-flex;
    justify-content: center;
    p {
      display: -webkit-flex;
      align-items: center;
      width: 33%;
      justify-content: space-between;
    }
  }

  .top-login-foot {
    line-height: 4rem;

    height: 4rem;
    background-color: #ff464e;
    font-size: 20px;
    color: white;
    span {
      line-height: 4rem;
      margin-left: 0.7rem;
    }
  }

  .my-order {
    background: white;
    .order-top {
      width: 90%;
      margin: 0 auto;
      border-bottom: 2px solid #f2f2f2;
      height: 1.8rem;
      .first {
        font-size: 15px;
        color: #333;
        line-height: 1.8rem;
      }
      .last {
        float: right;
        font-size: 14px;
        color: #999;
        line-height: 1.8rem;
      }
      .v {
        display: inline-block;
        width: 33.3333%;
        box-sizing: border-box;
        height: 0.6rem;
        text-align: center;
        line-height: 0.6rem;
        font-size: 15px;
        color: #333;
        border-right: 1px solid #f2f2f2;
      }
      .v:last-of-type {
        border: 0;
        margin-top: 0.65rem;
      }
    }
  }

  .multi-block1 {
    background: white;
    display: -webkit-flex;
    justify-content: space-around;
  }

  .multi-block1>a {
    display: inline-block;
    margin-top: 0.4rem;
    margin-bottom: 0.5rem;
  }

  .multi-block1>a>img {
    width: 40px;
    display: block;
    margin: 0 auto 0.42667rem;
  }

  .multi-block1>a>p {
    font-size: 12px;
    text-align: center;
    color: #666;
  }

  .footer {
    height: 2rem;
    background: white;
  }
</style>



