<template>
    <nav>
        <p>
            <span v-for="(item,index) in topMenu" :key="item" @click="goto(index)" 
            :class="{active:index==activeIndex,normal:index!=activeIndex}">{{item}}</span>
        </p>
    </nav>
</template>

<script>
    export default {
        props:['topMenu','actIndex'],
        data(){
            return {
                activeIndex:this.actIndex
            }
        },
        methods:{
            //根据下标选择跳转页面
            goto(index){
                this.activeIndex=index//将点击的按钮下标付给应该显示选中样式的下标
                if(index==0){
                    this.$router.push({
                        path:"/"
                    })
                }else{
                    this.$router.push({
                        path:"/DressToFoods",
                        query:{
                            id:index
                        }
                    })
                }
            }
        }
    }
</script>

<style lang="css" scoped>
    nav {
        overflow-x: scroll;
        width: 100%;
        border-top: 1px solid #f2f2f2;
    }
    
    nav>p {
        height: 38px;
        background: #fff;
        display: -webkit-flex;
        justify-content: space-around;
        width: 150%;
        line-height: 38px;
        font-size: 14px;
        color: #333;
    }
    .active{
        color:red;
        border-bottom: 4px solid #ff464e;
    }
    .normal{
        color: #666;
    }
</style>





