<template lang="html">

         

    <div id="global">

        <Loading :loadingFlag="loadingFlag"></Loading>
        
        <!--底部菜单组件-->
        <MenuBar :menubarData="menubarData"></MenuBar>
        <div class="classify" v-for="item in classifyData">
            <a :href="i.url" v-for="i in item.data[0].child"><img :src="i.pic" alt=""></a>
        </div>
        <ul class="goods">
            <li v-for="item in goodsData">
                <a class="banner" :href="item.target_url" v-show="item.shop_cover"><img :src="item.shop_cover"  alt=""></a>
                <div class="box">
                    <div class="list">
                        <div class="good" v-for="i in item.shop_goods">
                            <a :href="i.target_url">
                                <img :src="i.pic_url" alt=""><br/>
                                <p class="price">
                                <span class="newp">￥{{i.cprice}}</span>
                                <span class="delp">￥{{i.oprice}}</span>
                                </p>
                                <div class="goodName">
                                    {{i.title}}
                                </div>
                            </a>
                        </div>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
//引入底部菜单模块
import MenuBar from '../common/MenuBar'
export default {
    name: 'global',
    data() {
        return {
            classifyData:[],
            goodsData:[],
            jsonpPage: '1',
            loadingFlag:true
        }
    },
    methods: {
        getGoodsDate(){
            this.$http.jsonp('https://shop.juanpi.com/gsort',{
                params: {
                    key: '310',
                    type: '6',
                    zhouyi_ids: 'p8_c3_l1_18_51_5',
                    machining:'showshopgoods',
                    page:this.jsonpPage,
                    rows:'10',

                },
                jsonp:'callback',jsonpCallback:'gsort_callback'
            }).then(res=>{
                console.log(res);
                this.goodsData=this.goodsData.concat(res.body.list)
            })
        },
        getClassifyData(){
            this.$http.jsonp('https://jas.juanpi.com/pagetag/page',{
                params: {
                    unique:'module_ads,banner_ads',
                    cid:'310',
                    zy_id:'c3_l1_18_51_5',
                    platform:'m',
                    _:'1499952443518'
                },
                jsonp:'callback',jsonpCallback:'jsonp1'
            }).then(res=>{

                this.classifyData=res.body.module_ads.multi_block;

            })
        },
        //滚动加载函数 2820
            scrollLoad() {
                var _this = this;
                var base;
                var diff;
                console.log(document.documentElement.style.fontSize);
                window.onscroll = function() {
                    var dis = window.scrollTop || document.body.scrollTop || document.documentElement.scrollTop;
                    base = 135 * parseFloat(document.documentElement.style.fontSize)
                    diff = 28 * parseFloat(document.documentElement.style.fontSize);
                    console.log(dis/20);
                    if ((dis + diff) / base >= _this.jsonpPage) {
                        console.log("进入")
                        _this.jsonpPage++;

                        _this.getGoodsDate();
                        console.log(_this.goodsData);

                    }

                }

            }
    },
    computed:{
            menubarData:function(){
            return this.$store.getters.menubarData
            }
        },
    components:{
        MenuBar
    },
    created(){
        window.onscroll = null;
        this.getClassifyData();
        this.getGoodsDate();
        this.scrollLoad();
        console.log(this.jsonpPage)
    },
    mounted(){
        setTimeout(()=>{
            this.loadingFlag=false
        },800)
    }
}
</script>

<style lang="css">
body{
    background: #fff!important;
}
a{
    display: inline-block;
}
.classify{
    width: 100%;
}
.classify a{
    width: 50%;
}
.classify img{
    width: 100%;
}
.goods>li>a>img{
    width: 100%;
}
.banner{
    height: 7.5rem;
}
.list{
    padding-top: 0.7rem;
    height: 8rem;
    white-space: nowrap;
    width: auto;
    overflow: hidden;
    overflow-x: auto;
}
.good{
    display: inline-block;
    height: 7.5rem;
    width: 4.5rem;
    margin-left: 0.5rem;
}
.good a{
    height: 7.5rem;
    width: 100%;
}
.good img{
    height: 4.5rem;
    width: 4.5rem;
}
.price{
    text-align: center;
}
.delp{
    color: #bbb;
    text-decoration: line-through;
}
.goodName{
    white-space: normal;
    text-align: center;
}
</style>



