<script>
import {
    swiper,
    swiperSlide
} from 'vue-awesome-swiper'
require('swiper/dist/css/swiper.css')
export default {
    data(){
        return {
            id:this.$route.params.id ,
            name:this.$route.params.uname,
            product:[],
            uflag: false,
            //轮播图配置数据
            swiperOption: {
                initialSlide: '0',
                direction: 'horizontal',
                setWrapperSize:true,
                autoHeight:true,
                roundLengths:true,
                observer: true,//修改swiper自己或子元素时，自动初始化swiper
                observeParents: true,
            },
            flag:'',
            sizeNum:'',
            colorNum :"",
            tabNum:0,
            img:"",
            show:false,
            lv:true,
            ye:false,
            lan:false,
            size:["S","M","L","XL"],
            color:["绿粉色","杏粉蓝","姜黄色"],
            uploadSize:"",
            uploadColor:"",
            count:1,
            alertFlag:false
        }
    },
    computed:{
        isnum(){
            return this.$store.getters.allnum
        },
        flags(){
            if(this.uploadSize &&this.uploadColor){
                this.flag = true ;

            }else{

                this.flag = false ;
            }
            return this.flag ;
        },

        login(){
            return this.$store.getters.setIslogin
        },
        userName(){
            return this.$store.getters.userName
        },
        getStock(){
            return this.$store.getters.getStock
        },
    },
    components:{
        swiper,
        swiperSlide,
    },
    methods: {
        gocart(){
             this.$router.push({name:"shopCart"});
        },
        gotuan() {
            this.$router.push({name:"Tuan"})
        },
        goVip(){
            this.$router.push({name:"vip"})
        },
        tabSwitch(index){
            this.tabNum = index ;

        },
        activeColor(el,index){
            this.colorNum = index ;
            this.uploadColor = el;

        },
        activeSize(el,index){
            this.sizeNum = index ;
            this.uploadSize = el;
        },
        getHttp(){
            this.axios.get('http://139.199.107.198:8888/getDetail',{
                params:{
                    id:this.id
                }
            }).then((res)=>{
                // console.log(res);
                this.$nextTick(()=>{
                    this.img = res.data[0].slider[4]
                    this.product = res.data[0] ;
                })
            }, (err)=>{

            })
        },
        back(){
            history.back();
        },
        send(){

            if(this.flags){

                if(this.login){
                    this.$http.get("http://139.199.107.198:8888/updateCart",{
                            params:{
                                    goodsid:this.id,
                                    color:this.uploadColor,
                                    size:this.uploadSize,
                                    price:this.product.price,
                                    url:this.img,
                                    count:this.count,
                                    checked:true,
                                    uname:this.name,
                                    username:this.userName,
                                    title:this.product.title
                            }
                        }
                    ).then((res)=>{
                        console.log(res);
                        if(res.body){
                            this.$store.commit('ADD_CARTDATA',{
                                    goodsid:this.id,
                                    color:this.uploadColor,
                                    size:this.uploadSize,
                                    price:this.product.price,
                                    url:this.img,
                                    count:this.count,
                                    checked:'true',
                                    uname:this.name,
                                    username:this.userName,
                                    title:this.product.title
                                });
                        }
                    },(err)=>{
                        alert("网络丢失");
                        console.log(err);
                    });

                }else{
                    this.$router.push({name:"Login"})
                }
            }
        },
        add(){
            if(this.flags){
                if(this.count==5){
                    this.alertFlag = true ;
                    setTimeout( ()=>{
                        this.alertFlag = false ;
                    }, 1000);
                    return ;
                }
                this.count=this.count+1 ;
            }

        },
        reduce(){
            if(this.flags){
                if(this.count==1){
                    return ;
                }
                this.count=this.count-1 ;
            }

        }
    },
    created(){
        window.onscroll = null ;
        this.getHttp();
    },
}
</script>
