import Vue from 'vue'
import App from './App'
// 导入 router 实例
import router from './router'
//导入 vuex 实例
import store from './vuex'
import VueLazyload from 'vue-lazyload'
//引入重置样式表
import "../static/css/reset.css"
import "../static/js/viewport.js"
import "../static/css/animate.css"
//引入 vue-resource 模块
import VueResource from 'vue-resource'
import Loading from './common/loading'

// 引入vue-amap
import VueAMap from 'vue-amap';
Vue.use(VueAMap);

// 初始化vue-amap
VueAMap.initAMapApiLoader({
  // 高德的key
  key: '864e51aba8fe270368a90a42abb363b8',
  // 插件集合
  plugin: ['AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor']
});
//把资源模块安装在vue框架中
Vue.use(VueResource);
Vue.use(VueLazyload);
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: 'assets/logo.png',
  loading: './assets/R.png'

})


Vue.use(Loading)

import axios from 'axios'

Vue.prototype.axios = axios

import VueSwiper from 'vue-awesome-swiper'

Vue.use(VueSwiper)

Vue.config.productionTip = false

//自定义过滤器
Vue.filter('money', function(val,fu) {

    val = val.toString().replace(/\$|\,/g,'');
    if(isNaN(val)) {
      val = "0";
    }
    let sign = (val == (val = Math.abs(val)));
    val = Math.floor(val*100+0.50000000001);

    let cents = val%100;
    val = Math.floor(val/100).toString();
    if(cents<10) {
       cents = "0" + cents
    }
    for (var i = 0; i < Math.floor((val.length-(1+i))/3); i++) {
        val = val.substring(0,val.length-(4*i+3))+',' + val.substring(val.length-(4*i+3));
    }

    return (((sign)?'':'-') + fu + val + '.' + cents);
})


//自定义指令倒计时指令
Vue.directive('date', function (el, binding) {
    var interval = 1000;
    setInterval(function() {
        var now = new Date();
        var leftTime=binding.value.time*1000 -now.getTime();
        var leftsecond = parseInt(leftTime/1000);
        var day1=Math.floor(leftsecond/(60*60*24));
    	var hour=Math.floor((leftsecond-day1*24*60*60)/3600);
    	var minute=Math.floor((leftsecond-day1*24*60*60-hour*3600)/60);
    	var second=Math.floor(leftsecond-day1*24*60*60-hour*3600-minute*60);
        if(binding.value.bol){
            el.innerHTML =" &#xe74a; " +day1+"天"+hour+"时"+minute+"分"+second+"秒 &#xe74a; "
        }else{
            el.innerHTML =hour+":"+minute+":"+second
        }


    }, interval);
})



new Vue({
    el: '#app',
    router, //把创建出来的实例挂载进页面实例中
    store,
    template: '<App/>',
    components: {
        App
    }
})



