<template>
    <div id="">
        <div id="card-nav" class="nav card-nav">
            <ul>
                <li :class="{ active: isActive }" @click.stop="add">
                    <div class="nav-tab">
                        <h4>
                            钻石卡(享9.0折)
                        </h4>
                        <div class="nav-tab-sub">
                            <i class="discounted">
                            </i>
                            <span class="price">
                                199元
                            </span>
                            <span class="oprice">
                                ¥299
                            </span>
                        </div>
                    </div>
                    <i class="triangle">
                    </i>
                </li>
                <li :class="{ default: isDefault }" @click.stop="dele">
                    <div class="nav-tab">
                        <h4>
                            金卡(享9.5折)
                        </h4>
                        <div class="nav-tab-sub">
                            <i class="discounted">
                            </i>
                            <span class="price">
                                169元
                            </span>
                            <span class="oprice">
                                ¥269
                            </span>
                        </div>
                    </div>
                    <i class="triangle">
                    </i>
                </li>
            </ul>
        </div>
        <div class="tabImg" >
            <img src="../assets/vip1.jpg" alt="" v-show="isActive">
            <img src="../assets/vip2.png" alt="" v-show="isDefault">
            <img src="../assets/text.png" alt="">
        </div>
        <div class="agree-with-info">
            <span id="agree-with" class="checked">
                已阅读并同意
            </span>
            <a href="javascript:;" id="go-rules">
                卷皮VIP卡用户协议
            </a>
        </div>
        <a href="javascript:;" class="go-buy disabled" @click="goto()" >
            {{msg}}
        </a>
    </div>
</template>
<script>
export default {
    data(){
        return{
            isActive:true ,
            isDefault:false,
            msg:'开通钻石卡'
        }
    },
    methods:{
        add:function(){
            this.isActive = true ;
            this.isDefault = false;
            this.msg = '开通钻石卡'
        },
        dele:function(){
            this.isActive = false ;
            this.isDefault = true;
            this.msg = '开通金卡'
        },
        goto(){
            this.$router.push({name:"Login"})
        }
    }
}
</script>
<style lang="scss" scoped>
.nav {
    margin-top: -1px;
    padding-top: 1px;
    height: 3rem;
    font-size: 0;
    background-color: #1d1e22;
    box-sizing: border-box;
    .active{
        .nav-tab{
            background: linear-gradient(228deg,#f2faff,#e6f0ff);
            h4 {
                color: #1d1e22;
            }
            .nav-tab-sub{
                .discounted {
                    background-image: url("../assets/discounted-active.png");
                }
                .price {
                    color: #6375c4;
                }
                .oprice {
                    opacity: .3;
                    color: #465387;
                }
            }
        }
        .triangle {
            background: #ecf4ff;
        }

    }
    .default{
        .nav-tab{
            background: linear-gradient(-131deg,#ffeed7,#fffaf1);
            h4 {
                color: #1d1e22;
            }
            .nav-tab-sub{
                .discounted {
                    background-image: url("../assets/discounted-active.png");
                }
                .price {
                    color: #aa6a3d;
                }
                .oprice {
                    opacity: .4;
                    color: #93582e;
                }
            }
        }
        .triangle {
            background: #fff3e3;
        }

    }
    ul {
        position: fixed;
        z-index: 300;
        top: -1px;
        width: 100%;
        max-width: 16rem;
        padding-top: 1px;
        height: 3rem;
        box-sizing: border-box;

        li {
            float: left;
            position: relative;
            height: 3rem;
            width: 50%;
            background-color: #1d1e22;
            box-sizing: border-box;
            text-align: center;
            .nav-tab {
                position: relative;
                z-index: 2;
                padding-top: .4rem;
                padding-bottom: .3rem;
                height: 3rem;
                background-color: #30323d;
                box-sizing: border-box;
                h4 {
                    margin-bottom: 4px;
                    font-size: 14px;
                    font-weight: 700;
                    color: #fff;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
                .nav-tab-sub {
                    font-size: 0;
                    line-height: 1rem;
                    .discounted {
                        display: inline-block;
                        vertical-align: top;
                        height: 1rem;
                        width: 1.75rem;
                        background-image: url("../assets/discounted-default.png");
                        background-position: 50%;
                        background-repeat: no-repeat;
                        background-size: contain;
                    }
                    .price {
                        font-size: 14px;
                        font-weight: 700;
                        color: #fff;
                    }
                    .oprice {
                       font-size: .6rem;
                       color: #bbb;
                       opacity: .3;
                       text-decoration: line-through;
                    }
                }
            }
            .triangle {
                position: absolute;
                z-index: 1;
                left: 50%;
                bottom: 0;
                width: .6rem;
                height: .6rem;
                background: transparent;
                -webkit-transform: translate(-50%,30%) rotate(-45deg);
                transform: translate(-50%,30%) rotate(-45deg);
            }
        }
    }
}
.tabImg{
    img{
        width: 100%;
    }
}
.agree-with-info{
    position: fixed;
    z-index: 100;
    width: 16rem;
    bottom: 2.4rem;
    font-size: 0;
    background-color: #fff;
    border-top: 1px solid #ebebeb;
    color: #999;
    text-align: center;
    span {
        display: inline-block;
        position: relative;
        padding-top: .6rem;
        padding-bottom: .6rem;
        padding-left: .72533rem;
        height: .72533rem;
        line-height: .72533rem;
        font-size: .6rem;
        vertical-align: top;
    }
    .checked:before {
        background-image: url('../assets/checked.png');
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        width: .512rem;
        height: .512rem;

        background-position: 50%;
        background-repeat: no-repeat;
        background-size: contain;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
    }
}
.disabled {
    background-color: #dbdbdb;
}
.go-buy {
    display: block;
    position: fixed;
    z-index: 100;
    bottom: 0;
    width: 16rem;
    height: 48px;
    line-height: 48px;
    font-size: 18px;
    background-color: #ff464e;
    color: #fff;
    cursor: pointer;
    text-align: center;
}
</style>



