<template>
    <div class="shop-show-wrap">

        <router-link :to="{name:'StoreDisplay',params:{id:arr[num],name:item.title}}" v-for="item in showData" :key="item.key" class="common-floor">

            <div class="shop-show-top clear">

                <img :src="item.shop_logo">

                <p>

                    <span>{{item.title}}</span>

                    <span>{{item.coupon_tips}}</span>

                </p>

                <span>{{'Go'+'>'}}</span>

            </div>

            <div class="shop-show-bottom clear">

                <div class="shop-show-bottom-sub" >

                     <img v-lazy="item.pic_url">

                    <p class="price"><span>{{item.oprice}}</span><span>{{item.oprice}}</span></p>

                    <p class="name">{{item.title}}</p>

                </div>
                <div class="shop-show-bottom-sub" >

                     <img v-lazy="item.pic_url">

                    <p class="price"><span>{{item.oprice}}</span><span>{{item.oprice}}</span></p>

                    <p class="name">{{item.title}}</p>

                </div>

                <div class="line"></div>

            </div>

        </router-link>

    </div>
</template>

<script>
    export default {

        props:['showData'] ,
        data(){
            return {
                arr:[ 2901631 , 2770699 , 2901684 , 2608588],
                num:''
            }
        },
        methods: {
            randomNum:function(min ,max ){
                this.num = parseInt(Math.random()* (max-min+1)+min);
            }
        },
        created() {
            this.randomNum(0,3);
            console.log(this.num);
        }

    }
</script>


<style lang="css" scoped>
    .shop-show-wrap>a {
        display: block;
    }

    .shop-show-top,
    .shop-show-bottom {
        background: white;
    }

    .shop-show-bottom {
        position: relative;
    }

    .shop-show-top>* {
        float: left;
    }

    .shop-show-top>img {
        width: 1.536rem;
        height:1.536rem;
        margin-left: .5rem;
        margin-right: .3rem;
        margin-top: .4rem;
    }

    .shop-show-top>p {
        margin: .4rem 0;
    }

    .shop-show-top>p>span:first-of-type {
        display: block;
        width: 9.6rem;
        height: .7rem;
        margin-bottom: .1rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        font-size: .59733rem;
        color: #666;
    }

    .shop-show-top>p>span:last-of-type {
        font-size: .512rem;
        color: #999;
    }

    .shop-show-top>span:last-of-type {
        font-size: .512rem;
        color: #999;
        line-height: 2.07rem;
        margin-left: 2.25rem;
    }

    .shop-show-bottom>.shop-show-bottom-sub {
        float: left;
        margin: 0 .5rem;
        max-width: 6.976rem;
    }

    .shop-show-bottom-sub>img {
        width: 6.976rem;
        margin-bottom: .34133rem;
    }

    .shop-show-bottom-sub>.price>span:first-of-type {
        color: #ff464e;
        font-size: .64rem;
    }

    .shop-show-bottom-sub>.price>span:last-of-type {
        color: #999;
        margin-left: .256rem;
        font-size: .46933rem;
        text-decoration: line-through;
    }

    .shop-show-bottom-sub>.name {
        font-size: .512rem;
        color: #666;
        margin: .2rem 0 .6rem 0;
    }

    .shop-show-bottom>.line {
        width: 1px;
        background: #f2f2f2;
        height: 9.2rem;
        position: absolute;
        left: 7.95rem;
        top: 0;
    }

    .common-floor {
        margin-bottom: .5rem;
    }

    img{
    -webkit-transition: all 2s;
    -o-transition: all 2s;
    transition: all 2s;
    }
    img[lazy=loading] {
        opacity: .5;
        background: url("../assets/R.png");
        width: 6.976rem;
    }
    img[lazy=error] {
    
    }
    img[lazy=loaded] {
    opacity: 1;
    }
</style>



