<template>
    <div class="">
        <header class="head" >
            <div class="fixtop">
                <!-- 未开启“未注册下单”功能及空购物车时不显示返回按钮 -->
                <span class="iconfont iconfanhui" v-show="lengthBol" @click="goBack">
                    &#xe60e;
                </span>
                <span id="t-index">
                    购物车
                </span>
                <span id="t-user">
                    编辑
                </span>
            </div>
        </header>
    </div>
</template>
<script>
export default {
    computed:{
        lengthBol(){
            return this.$store.getters.lengthBol
        },
    },
    methods:{
        goBack(){
            history.back()
        }
    }
}
</script>
<style lang="scss" scoped>
.head {
    z-index: 199;
    height: 2.2rem;
    width: 100%;
    overflow: inherit;
    border-bottom: 1px solid #ebebeb;
    .fixtop {
        z-index: 999;
        height: 2.2rem;
        width: 100%;
        background: #fff;
        position: absolute;
        .iconfanhui{
            font-size: 1.35rem;
            font-weight: 500;
            position: absolute;
            left: 0;
            bottom: 0;
            height: 100%;
            display: block;
            line-height: 2.1rem
        }
        #t-index {
            cursor: default;
            width: 100%;
            z-index: -1;
            padding: 0 1.536rem;
            height: 2.2rem;
            line-height: 2.2rem;
            font-size: .9rem;
            box-sizing: border-box;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            text-align: center;
            color: #333;
            position: absolute;
            display: block;
        }
        #t-user {
            position: absolute;
            right: 0;
            bottom: 0;
            height: 100%;
            line-height: 2.1rem;
            font-size: .75rem;
            color: #4a4a4a;
            padding-right: .7rem;
            padding-top: .5rem;


        }
    }
}
</style>



