<template>
    <div>
        <div class="top">
            <span @click="back()"></span>
            {{words}}
        </div>

        <!--<ul class="nav" v-if="flag">
            <li @click="zong(flag)" :class="{red:b}">综合</li>
            <li @click="pai(flag)"  :class="{red:a}">
                价格
                <span class='nav-price' ref='price'></span>
            </li>
            <li @click="xiao(flag)" :class="{red:c}">销量</li>
            <li>仅看有货</li>
        </ul>-->


        <ul class="goods-list">
            <li v-for="elem in getData "  class="fl list">
                <img :src="elem.pic_url" alt="">
                <div class="list-price">
                    <span class="price-new">{{elem.tuan_price | money('￥')}}</span>
                    <span class="price-ping">拼</span>
                    <span class='price-oprice'>{{elem.oprice}}</span>
                </div>
                <p>{{elem.title}}</p>
            </li>

        </ul>
    </div>
    
</template>


<script>
    export default {
        data(){
            return {
                getData:[],
                flag:true,
                words:this.$route.params.id,
                jsonpParams:[{
                        key:'zuihoufengqiang',
                        type:1,
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'hotcoupon',
                        page:1,
                        rows:'10',
                        dtype:'JSONP',
                        cm:1,
                        cm_channel:4
                    },
                    {
                        key:1603,
                        type:50,
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'hotcoupon',
                        page:1,
                        rows:10,
                        dtype:'JSONP'
                    },
                    {
                        key:'yugao',
                        type:1,
                        zhouyi_ids:'p8_c3_l1_18_51_5',
                        machining:'yugao',
                        page:1,
                        rows:10,
                        dtype:'JSONP',
                        cm:1,
                        cm_channel:16
                    }
                    
                ],
                index:0
            }
        },
        methods:{
            getDataList(){
                this.$http.jsonp('https://shop.juanpi.com/gsort',{
                    params:this.jsonpParams[this.index]
                }).then(function(res){
                    // console.log(res.body)
                    this.getData=res.body.list
                })
            },
            back(){
                window.history.back();
            }
        },
        created(){
            if(this.words=="最后疯抢"||this.words=="即将上线"||this.words=="9.9包邮"||this.words=="拼团"){
                if(this.words=="最后疯抢")
                    this.index=0;
                else if(this.words=="9.9包邮")
                    this.index=1;
                else
                    this.index=2;
                this.getDataList();
            }else{
                // this.getDataList1()
            }
                
        }
    }
</script>

<style lang="scss" scoped>
    .top{
        height: 1.5rem;
        background-color: white;
        font-size:18px;
        line-height: 1.5rem;
        text-align: center;
        color:#333;
        position:relative;
        span{
            position:absolute;
            width:24px;
            height: 24px;
            background:url(../assets/btn-back.png)no-repeat;
            background-size: 24px 24px;
            left: .3rem;
            top: .35rem;
        }
    }
    .goods-list{
        display: flex;
        flex-wrap:wrap;
        justify-content:space-between;

        .list{
            width: 49.5%;
            background: #fff;
            margin-top: .1rem;
            padding-bottom: .6rem;

            img{
                width: 100%;
            }

            p{
                width: 96%;
                margin-top: 2px;
                font-size: 0.55rem;
                font-weight: 300;
                color: #333;
                background: hsla(0,0%,100%,.8);
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                -o-text-overflow: ellipsis;
                margin-left: 8px;
                height: 20px;
                line-height: 20px
            }
            .list-price{
                width: 92%;
                position: relative;
                bottom: 0;
                margin-left: 8px;
                margin-right: 8px;
                margin-top: 0;
                line-height: 24px;
                height: 24px;

                .price-new{
                    font-size: .65rem;
                    color: #ff464e;
                    margin-right: 4px;
                    vertical-align: middle;
                }

                .price-ping{
                    display: inline-block;
                    height: .6rem;
                    line-height: .6rem;
                    padding: 2px;
                    padding-bottom: 1px;
                    vertical-align: middle;
                    font-size: 10px;
                    background-color: #ff464e;
                    color: #fff;
                    margin-left: 0;
                    margin-right: .25rem;
                }
                
                .price-oprice{
                    vertical-align: middle;
                    text-decoration:line-through;
                    color: #bbb;
                }
            }
        }
    }



    // .nav{
    //     display: flex;
    //     justify-content:space-between;
    //     width:100%;
