import Vue from 'vue'
import Router from 'vue-router'


import Home from '@/components/Home'
// import DressToFoods from '../pages/DressToFoods.vue'
import StoreDisplay from '../pages/StoreDisplay/StoreDisplay'
import CommonTwoImg from '../common/CommonTwoImg.vue'
import ProductDetails from '../pages/ProductDetails/ProductDetails'
import Tuan from '../common/tuan'
import Vip from '../common/vip'
import Mine from '../components/Mine'
import Register from '../pages/Register'
import Login from '../pages/Login'
import Cart from '../pages/AppCart/cart'
import shopCart from '../pages/shopCart/shopcart.vue'
import Zhifacang from '@/components/Zhifacang'
import Global from '@/components/Global'
import Search from "@/common/search"
import map from "@/common/map"
//懒加载，需要的时候，才会加载，一开始不会加载
const DressToFoods = resolve => require(['../pages/DressToFoods.vue'], resolve)
//加载我们的插件，这个时候vue-router才能使用
Vue.use(Router)

const routes = [
    {
        path: '/',
        redirect: '/Home'
        
    },
    {
        path: '/DressToFoods',
        name: 'DressToFoods',
        component: DressToFoods
    },
    {
        path: '/StoreDisplay/:id',
        name: 'StoreDisplay',
        component: StoreDisplay
    },
    {
        path: '/Home',
        name: 'Home',
        component: Home
    },
    {
        path: '/CommonTwoImg/:id',
        name: 'CommonTwoImg',
        component: CommonTwoImg
    },
    {
        path: '/ProductDetails/:id',
        name: 'ProductDetails',
        component: ProductDetails
    },
    {
        path: '/Tuan',
        name: 'Tuan',
        component: Tuan
    },
    {
        path:"/vip",
        name:"vip",
        component:Vip
    },
    {
        path:"/Mine",
        name:"Mine",
        component:Mine
    },
    {
        path:"/Register",
        name:"Register",
        component:Register
    },
    {
        path:"/Login",
        name:"Login",
        component:Login
    },
    {
        path:"/cart",
        name:'cart',
        component:Cart
    },
    {
        path:"/shopCart",
        name:'shopCart',
        component:shopCart
    },
     {
        path:"/Zhifacang",
        name:'Zhifacang',
        component:Zhifacang
    },
    {
        path:"/Global",
        name:'Global',
        component:Global
    },
    {
        path:"/serach",
        name:'search',
        component:Search
    },
    {
        path:"/map",
        name:'map',
        component:map
    },
    
  ]

const router = new Router({
    routes ,
    mode: 'history', //切换路径模式，变成history模式,hash值前面的#没有
})

export default  router



