<template>
    <div class="login">

        <HomeHeader :topbanner="topbanner"></HomeHeader>


        <div class="top-head">
            <span class="go-back" @click="back()"></span>登录
            <span class="go-regis" @click="goRegis">注册</span>
        </div>


        <ul class="input_box">

            <li><input type="text" placeholder="手机号/邮箱/用户名" autofocus v-model="phone"/></li>
            <li><input type="password" placeholder="密码"  v-model="password"/></li>

        </ul>   


        <div class="next-step" @click="login()">登录</div>

        <div class="msgbox" v-show="msg">{{msg}}</div>


        
        
    </div>
</template>


<script>
    import HomeHeader from '../common/HomeHeader'
    export default {
        data(){
            return {
                msg:"",
                phone:"",
                password:""
            }
            
        },

         methods:{
            back(){
                window.history.back();
            },
            //验证登录信息
            login(){
                this.$http.get("http://139.199.107.198:8888/login",{
                    params:{
                        user:this.phone,
                        password:this.password
                    }
                }).then(res=>{
                    if(res.body=="1"){
                        //用户名密码匹配成功
                        this.loginSuccess();

                        //将用户名和jp_拼接存入cookie
                        this.saveCookie();
                        //获取当前登陆用户的购物车数据
                        this.getCartData();
                        // 将手机号拼接存入到vuex username中
                        this.$store.commit("CHANGE_USERNAME","jp_"+this.phone);
                    }else{
                        //用户名密码错误
                        this.loginError();
                    }
                })
            },
            goRegis(){
                this.$router.push({name:'Register'})
            },

            loginSuccess(){
                var _this=this
                this.msg="登陆成功";
                setTimeout(function(){
                    _this.msg="";
                },2000);
            },
            loginError(){
                var _this=this
                this.msg="用户名密码错误或未被注册";
                setTimeout(function(){
                    _this.msg="";
                },2000);
            },
            saveCookie(){
                var oDate = new Date();
                // 设置距离当前时间多少天后cookie过期
                oDate.setDate(oDate.getDate() + 30);
                // 设置cookie及过期时间
                document.cookie="html1705=jp_"+this.phone+";expires=" + oDate;
                console.log(document.cookie)
                // var arr=document.cookie.split(";")//先用;把cookie分割成数组
                // var obj={};//定义一个空对象用来存储cookie的键值对
                
                // arr.forEach(function(item,index){
                //     //先要去除每个字符串里面的首尾空格,在以=号分割把每项转为数组
                //     //cookie第一条往后都会在头部添加一个空格,所以要先用trim去除字符串空格
                //     var a=item.trim().split("="); 
                //     obj[a[0]]=a[1];//分割出来的数组，第一个元素为key,第二个为value，存入对象
                // })
                // console.log(obj);
            },
            //将登陆用户的购物车数据拿下来
            getCartData(){
                this.$http.get("http://139.199.107.198:8888/getCart",{
                    params:{
                        username:"jp_"+this.phone
                    }
                }).then(res=>{
                    console.table(res.body);
                    var _this=this;
                    //将购物车数据存入vuex
                    this.$store.commit("ISLOGINDATA",res.body);
                    //改变登录状态标志
                    this.$store.commit("CHANGEISLOGIN",true)
                    
                    setTimeout(function(){
                        //用户购物车数据下来后跳转到登我的页面
                        _this.$router.push({name:'Mine'})
                    },2500)
                })
            }
            
         },

         computed:{
            topbanner:function(){
                return this.$store.getters.topBanner
            }
        },
        components:{
           HomeHeader
        }
    }
</script>


<style lang="scss" scoped>
    .login{
        background: white;
        height: 1000px;
    }
    .top-head{
        height: 1.8rem;
        font-size:18px;
        line-height: 1.8rem;
        text-align: center;
        color:white;
        position:relative;
        background: #ff464e;
        border-bottom:1px solid #f2f2f2;
        .go-back{
            position:absolute;
            width:24px;
            height: 24px;
            background:url(../assets/mine_back.png)no-repeat;
            background-size: 24px 24px;
            left: .5rem;
            top: .4rem;
        }
        .go-regis{
            font-size:14px;
            float: right;
            color: white;
            height: 1.8rem;
            margin-right: .3rem;
        }
    }


    .input_box{
        margin-bottom:.7rem;
        li{
            width: 94%;
            border-bottom:2px solid #f2f2f2;
            margin:0 auto;
            input{
                border:0;
                width:100%;
                height: 1.8rem;
                font-size:14px;
            }
        }
    }


    .next-step{
        text-align: center;
        height: 1.8rem;
        background: #ff464e;
        line-height: 1.8rem;
        width: 96%;
        margin:0 auto;
        font-size:18px;
        color: white;
    }


    .msgbox{
        position:fixed;
        bottom:20rem;
        height: 2.5rem;
        width: 100%;
        background: rgba(0,0,0,.6);
        margin:0 auto;
        color: white;
        line-height: 2.5rem;
        text-align: center;
        font-size:15px;
    }
</style>




