<template>
    <div id="">

        <ul class="nav">
            <li @click="zong(flag)" :class="{red:b}">综合</li>
            <li @click="pai(flag)"  :class="{red:a}">
                价格
                <span class='nav-price' ref='price'></span>
            </li>
            <li @click="xiao(flag)" :class="{red:c}">销量</li>
            <li>仅看有货</li>
        </ul>
        <div class="">
            <ul class="goods-list">
                <li v-for="elem in getData "  class="fl list" @click="GoTo(elem.goods_id,shopname)" >
                    <img v-lazy="elem.opgpicurl" alt="">
                    <div class="list-price">
                        <span class="price-new">{{elem.tuan_price | money('￥')}}</span>
                        <span class="price-ping">拼</span>
                        <span class='price-oprice'>{{elem.oprice}}</span>
                    </div>
                    <p>{{elem.title}}</p>
                    <h3>{{allData.name}}</h3>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    props:['msg','shopname'],
    data(){
        return{
            flag:"" ,
            a:false,
            b:false,
            c:false
        }
    },
    methods:{
        GoTo(goodsId,shopname){
            this.$router.push({name:'ProductDetails',params:{id:goodsId,uname:shopname}});
        },
        pai(flag){

            this.flag =  flag>0? -1:1 ;
            if(this.flag==1){
                this.$refs.price.style.backgroundPositionX= "-0.45rem";
            }
            if(this.flag==-1){
                this.$refs.price.style.backgroundPositionX= "-0.9rem";
            }
            this.a = true ;
            this.b = false ;
            this.c = false
        },
        zong(){
            this.$refs.price.style.backgroundPositionX= "0";
            this.flag = 4
            this.a = false ;
            this.b = true ;
            this.c = false

        },
        xiao(flag){
            this.$refs.price.style.backgroundPositionX= "0"
            this.flag = 3
            this.a = false ;
            this.b = false ;
            this.c = true
        }
    },

    computed:{
        getData:{
            get:function () {
                    var arr = this.$store.getters.allData;
                    // var newArr = this.msg ;
                    var _this = this
                    // var arr = this.msg;
                    return (function(flag){
                     if(flag<=3){
                         for(var i=0;i<arr.length-1;i++){
                             for(var j=0;j<arr.length-1-i;j++){
                                 if(flag==1){
                                     if(arr[j].tuan_price>arr[j+1].tuan_price){
                                         var temp=arr[j+1];
                                         arr[j+1]=arr[j];
                                         arr[j]=temp;
                                     }
                                 }else if(flag==-1){
                                     if(arr[j].tuan_price<arr[j+1].tuan_price){
                                         var temp=arr[j+1];
                                         arr[j+1]=arr[j];
                                         arr[j]=temp;
                                     }
                                 }else if(flag==3){
                                     if(arr[j].join_number<arr[j+1].join_number){
                                         var temp=arr[j+1];
                                         arr[j+1]=arr[j];
                                         arr[j]=temp;
                                     }
                                 }
                             }
                         }
                     }else{
                        console.log(_this.msg);
                        arr = _this.msg

                     }
                     return arr ;

                    })(this.flag)
            },
            set: function (newValue) {

            }
        }
        ,
        allData(){
            //return this.$store.state.money
            return this.$store.getters.allData
        },
    }
}

</script>
<style lang="scss" scoped>
    .red{
        color: red;
    }
    .nav{
        display: flex;
        justify-content:space-between;
        width:100%;
        line-height: 1.8rem;
        background-color: #fff;
        border-bottom: 1px solid #ebebeb;
        font-size: .65rem;
        li{
            width:25%;
            text-align: center;

        }
        .nav-price{
            background: url("../../assets/sort-sprite.png") no-repeat;
            display: inline-block;
            margin-left: .25rem;
            height: .68rem;
            width: .43rem;
            background-size:2.45rem ;
            vertical-align: middle;
            background-position-x: 0;
        }
    }
    .goods-list{
        display: flex;
        flex-wrap:wrap;
        justify-content:space-between;

        .list{
            width: 49.5%;
            background: #fff;
            margin-top: .1rem;
            padding-bottom: .6rem;

            img{
                width: 100%;
            }

            p{
                width: 96%;
                margin-top: 2px;
                font-size: 0.55rem;
                font-weight: 300;
                color: #333;
                background: hsla(0,0%,100%,.8);
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                -o-text-overflow: ellipsis;
                margin-left: 8px;
                height: 20px;
                line-height: 20px
            }
            .list-price{
                width: 92%;
                position: relative;
                bottom: 0;
                margin-left: 8px;
                margin-right: 8px;
                margin-top: 0;
                line-height: 24px;
                height: 24px;

                .price-new{
                    font-size: .65rem;
                    color: #ff464e;
                    margin-right: 4px;
                    vertical-align: middle;
                }

                .price-ping{
                    display: inline-block;
                    height: .6rem;
                    line-height: .6rem;
                    padding: 2px;
                    padding-bottom: 1px;
                    vertical-align: middle;
                    font-size: 10px;
                    background-color: #ff464e;
                    color: #fff;
                    margin-left: 0;
                    margin-right: .25rem;
                }

                .price-oprice{
                    vertical-align: middle;
                    text-decoration:line-through;
                    color: #bbb;
                }
            }
        }
    }
    img{
    -webkit-transition: all 2s;
    -o-transition: all 2s;
    transition: all 2s;
    }
    img[lazy=loading] {
        opacity: .5;
        background: url("../../assets/R.png");
        width: 6.976rem;
    }
    img[lazy=error] {

    }
    img[lazy=loaded] {
    opacity: 1;
    }
</style>



