<template>
    <div id="">
        <headTop>
        </headTop>
        <div id="" v-show="!lengthBol">

            <div class="ajaxHtml">
                <div class="my-bag" id="goodsList">
                    <div class="item-empty">
                        <img src="//jp.juancdn.com/jpwebapp_v1/images_v1/shopping/empty-cart.png">
                        <div class="b-title">
                            看到喜欢的就带回家吧
                        </div>
                        <a class="btn" @click="goHome">
                            今日上新
                        </a>
                    </div>
                </div>
            </div>
        </div>
        <div class="" v-show="lengthBol">
            <div class="ajax">
                <div class="item_cart" v-for="(elem,index) in getAllCart" >
                    <div class="item_head_box">
                        <div class="item_head">
                            <div class="head_radio" @click="Choice(elem)">
                                <span class="iconfont icon-weixuanzhong " :class="{'active':elem.checked=='true'}" >
                                </span>
                            </div>
                            <div class="head_type">
                                <a class="bc" >
                                    <span class="iconfont icon-dianpu">
                                        &#xe631;
                                    </span>
                                    <span>
                                        {{elem.uname}}
                                    </span>
                                    <span class="iconfont icon-liebiaojiantou ">
                                        &#xe670;
                                    </span>
                                </a>
                            </div>
                            <div class="head_action">
                            </div>
                        </div>
                    </div>
                    <div class="item_body">
                        <div class="item_group">
                            <ul class="good_list">
                                <li class="good_item" >
                                    <div class="good_radio" @click="Choice(elem)">
                                        <span class="iconfont icon-weixuanzhong ":class="{'active':elem.checked=='true'}">
                                        </span >
                                    </div>
                                    <a  class="good_info clear">
                                        <div class="pic">
                                            <img :src="elem.url">
                                        </div>
                                        <div class="info_box">
                                            <div class="info_first clear">
                                                <p class="title fl">
                                                    {{elem.title}}
                                                </p>
                                                <p class="price fr">
                                                    {{elem.price}}
                                                </p>
                                            </div>
                                            <div class="info_second clear">
                                                <p class="type fl">
                                                    {{elem.color}}&nbsp{{elem.size}}
                                                </p>
                                                <p class="old fr">
                                                    单独购买￥{{parseInt(elem.price.substring(1))+10}}
                                                </p>
                                            </div>
                                            <div class="info_third clear">
                                                <p class="num fl">
                                                    <i class="decrease " @click="reduce(elem)">
                                                        -
                                                    </i>
                                                    <input class="amount" type="text" name="amount" v-model="elem.count" data-totalnum="4"
                                                    readonly="true">
                                                    <i class="increase " @click="add(elem)">
                                                        +
                                                    </i>
                                                </p>
                                                <p class="del hide fr">
                                                    <span class="iconfont icon-shanchu" @click="del(elem)">
                                                        &#xe632;
                                                    </span>
                                                </p>
                                            </div>
                                        </div>
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div class="bag-total">
                    <div class="sel-all" >
                        <span class="iconfont icon-yixuanzhong" :class="{'active':isAllChecked=='true'}" @click="allChecked">

                        </span>
                        全选
                    </div>
                    <div class="bag-money">
                        <p class="count">
                            <span class="zje">
                                合计
                                <em class="p" id="myBagTotal">
                                    {{(allprice-isnum*10).toFixed(2)}}
                                </em>
                            </span>
                            <br>
                            <span class="s">
                                总额:
                                <em id="save">
                                    {{allprice.toFixed(2)}}
                                </em>
                                &nbsp;立减:
                                <em id="save">
                                    {{(isnum*10).toFixed(2)}}
                                </em>
                            </span>
                        </p>
                    </div>
                    <a href="javascript:;" class="go_pay fr" @click="Settlement">
                        去结算({{isnum}})
                    </a>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import headTop from "../cartTop/header.vue"
export default {
    data(){
        return{
            num :''
        }
    },
    methods:{
        goHome() {
            this.$router.push({name:'Home'})
        },
        Choice(elem){
            this.$store.commit('CHECKEDBOL',elem)
        },
        allChecked(){
            this.$store.commit('ALLCHECKED')
        },
        Settlement(){
            this.$router.push({name:"cart"})
        },
        del(elem){
            this.$http.get("http://139.199.107.198:8888/delete",{
                params:{
                    color:elem.color,
                    goodsid:elem.goodsid,
                    size:elem.size,
                    username:elem.username
                }
            }).then((res)=>{

                    console.log("delete =>VUEX ")
                    if(res.body==1){
                        this.$store.commit('DELETE',elem)
                    }

            },(err)=>{
                console.log(err);
            })
        },
        add(elem){
            if(elem.count==5){
                this.$router.push({name:'Home'})
            }else{
                elem.count=elem.count +1 ;
                this.$http.get("http://139.199.107.198:8888/updateCart",{
                    params:{
                        goodsid:elem.goodsid,
                        color:elem.color,
                        size:elem.size,
                        price:elem.price,
                        url:elem.url,
                        count:elem.count,
                        checked:true,
                        uname:elem.uname,
                        username:elem.username,
                        title:elem.title,
                        bol:true

                    }
                }).then((res)=>{
                    console.log(res);
                    this.$store.commit('ADDSHOPCARTDATA',elem)
                })
            }
        },
        reduce(elem){
            if(elem.count==1){
                this.$router.push({name:'Home'})
            }else{

                elem.count=elem.count -1 ;
                this.$http.get("http://139.199.107.198:8888/updateCart",{
                    params:{
                        goodsid:elem.goodsid,
                        color:elem.color,
                        size:elem.size,
                        price:elem.price,
                        url:elem.url,
                        count:elem.count,
                        checked:true,
                        uname:elem.uname,
                        username:elem.username,
                        title:elem.title,
                        bol:true
                    }
                }).then((res)=>{
                    console.log(res);
                    this.$store.commit('REDUCE_CARTDATA',elem)
                })
            }
        }
    },
    components:{

        headTop
    },
    computed:{
        isnum(){
            return this.$store.getters.allnum
        },
        isAllChecked(){
            return this.$store.getters.isAllChecked
        },
        getAllCart(){
            return this.$store.getters.getCartData
        },
        allprice(){
            return this.$store.getters.allprice
        },
        lengthBol(){
            return this.$store.getters.lengthBol
        }
    }

}
</script>
<style lang="scss" scoped>
