                        width: .9rem;
                        height: .9rem;
                    }
                }

                .head_type {
                    -webkit-box-flex: 1;
                    -ms-flex: 1;
                    flex: 1;
                    font-size: .7rem;
                    color: #333;
                    overflow: hidden;
                    white-space: nowrap;
                    .icon-dianpu{
                        display: inline-block;
